import request from "@/utils/request";
export default {
  data() {
    return {
      icon: "",
      history: {},
    };
  },
  methods: {
    async getHistory() {
      await request
        .get("/history")
        .then((res) => {
          console.log(res);
          this.history = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  async mounted() {
    this.getHistory();
  },
};
